<template>
  <div class="box">
    <div class="content">
      <h4 class="is-uppercase">{{ $t("menuItems.subheaderItems") }}</h4>
      <p>
        {{ $t("menuItems.descriptionItems") }}
      </p>
    </div>

    <div class="buttons">
      <button class="button" @click="modalToggle(null)">Add item</button>
      <MenuItemsUpload />
    </div>

    <h4
      class="subtitle is-4 has-text-danger"
      v-if="!(menuItems && menuItems.length > 0)"
    >
      {{ $t("menuItems.emptyMessageItems") }}
    </h4>
    <div v-else class="table-container">
      <table class="table is-hoverable is-fullwidth">
        <thead>
          <tr class="is-uppercase">
            <th>{{ $t("menuItems.table.rank") }}</th>
            <th v-for="field in fields" :key="field">
              {{ $t(`menuItems.table.${field}`) }}
            </th>
            <th>{{ $t("menuItems.table.image") }}</th>
            <th>{{ $t("menuItems.table.edit") }}</th>
            <th>{{ $t("menuItems.table.delete") }}</th>
          </tr>
        </thead>

        <draggable v-model="myList" tag="tbody">
          <tr v-for="item in myList" :key="item.id">
            <th>{{ item.rank }}</th>

            <td v-for="field in fields" :key="field">
              <span>{{ item[field] }}</span>
            </td>
            <td>
              <!-- https://unicode-table.com/en/sets/check/ -->
              {{ item["image"] ? "&#9989;" : "&#10060;" }}
            </td>
            <td>
              <button
                class="button is-light is-small"
                @click="modalToggle(item.id)"
              >
                <span class="icon is-small">
                  <i class="fas fa-edit"></i>
                </span>
              </button>
            </td>
            <td>
              <button
                class="button is-light is-small"
                @click="deleteItem(item)"
              >
                <span class="icon is-small">
                  <i class="fas fa-trash"></i>
                </span>
              </button>
            </td>
          </tr>
        </draggable>
      </table>
    </div>
    <!-- ================= MODAL PART ================= -->
    <MenuItemsModal
      v-if="modal.show"
      :propsMenuItem="selectMenuItem(modal.id)"
      @modalClose="modalToggle(null)"
    />
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from "vuex";
import _ from "lodash";
import draggable from "vuedraggable";
import MenuItemsUpload from "@/components/MenuItemsUpload";
import MenuItemsModal from "@/components/MenuItemsModal";
import { getID } from "@/helpers/functions";

export default {
  name: "MenuItemsTable",
  components: { draggable, MenuItemsUpload, MenuItemsModal },

  data() {
    return {
      fields: ["groupName", "name", "description", "weight", "price"],
      modal: {
        show: false,
        id: null,
      },
    };
  },
  computed: {
    ...mapState(["menuItems", "menuGroups"]),
    ...mapGetters(["menuItemsGroupped"]),

    myList: {
      get() {
        const menuItemsMerged = this.merge(this.menuItems, this.menuGroups);
        return _.orderBy(menuItemsMerged, "rank", "asc");
      },
      set(value) {
        const temp = value.map((item, index) => ({
          ...item,
          rank: index + 1,
        }));
        this.saveMenuItems(temp);
      },
    },
  },
  methods: {
    ...mapActions(["saveMenuItems", "deleteMenuItem", "deleteItemImage"]),

    merge(menuItems, menuGroups) {
      // https://stackoverflow.com/questions/46849286/merge-two-array-of-objects-based-on-a-key
      const temp1 = menuItems.map((item) => {
        const group = menuGroups.find(
          (group) => group.id === item.group && group
        );
        return {
          ...item,
          rank: (group?.rank || 0) * 100 + (item?.rank || 0),
          groupName: group?.name || "set group",
        };
      });
      return temp1;
    },

    selectMenuItem(id) {
      if (id) {
        return this.menuItems.filter((item) => item.id == id)[0];
      } else {
        return {
          rank: 1,
          group: null,
          name: null,
          description: null,
          weight: null,
          price: null,
          image: null,
          id: getID(),
        };
      }
    },

    modalToggle(id) {
      this.modal.show = !this.modal.show;
      this.modal.id = id;
      this.fileImage = { name: null, image: null };
      this.selectMenuItem(id);
    },

    async deleteItem(item) {
      const { id, image } = item;
      await this.deleteMenuItem({ id });
      image && (await this.deleteItemImage({ id }));
    },
  },
};
</script>

<style lang="scss" scoped>
.textarea {
  resize: none;
}
</style>