<template>
  <div class="file mb-2 is-white">
    <label class="file-label">
      <input
        class="file-input"
        type="file"
        name="resume"
        ref="fileMenu"
        @click="resetFileMenu"
        @change="uploadFileMenu"
      />
      <span class="file-cta">
        <span class="file-icon">
          <i class="fas fa-upload"></i>
        </span>
        <span class="file-label"> Choose a CSV </span>
      </span>
    </label>
  </div>
</template>

<script>
import csv from "csvtojson";
import { getID } from "@/helpers/functions";

export default {
  name: "MenuItemsUpload",
  data() {
    return {
      fileMenu: {
        name: null,
        items: [],
      },
    };
  },
  methods: {
    //https://www.positronx.io/understand-html5-filereader-api-to-upload-image-and-text-files/
    //https://learn.javascript.ru/file
    async uploadFileMenu(e) {
      this.fileMenu.name = e.target.files[0];
      const reader = new FileReader();
      reader.onload = async () => {
        // console.log("reader 1", reader.result);
        const json = await csv().fromString(reader.result);
        // console.log("reader 2", json);
        this.fileMenu.items = json.map((item) => ({ ...item, id: getID() }));
        // console.log("reader 3", this.fileMenu.items);
        await this.$store.dispatch("saveMenuItems", this.fileMenu.items);
      };
      reader.readAsText(this.fileMenu.name);
    },
    resetFileMenu() {
      this.$refs.fileMenu.value = "";
    },
  },
};
</script>

