<template>
  <div class="box">
    <div class="columns">
      <div class="column is-4">
        <!-- TEXT PART  -->
        <div class="content">
          <h4 class="is-uppercase">{{ $t("menuItems.subheaderGroups") }}</h4>
          <p>
            {{ $t("menuItems.descriptionGroups") }}
          </p>
        </div>

        <!-- INPUT FIELD PART  -->
        <div class="field has-addons">
          <div class="control">
            <input
              v-model="name"
              class="input"
              type="text"
              placeholder="Create menu group"
            />
          </div>
          <div class="control">
            <a class="button is-primary" @click="createMenuGroup(name)">
              Create
            </a>
          </div>
        </div>
      </div>
      <div class="column">
        <!-- TAGS PART  -->
        <h4
          class="subtitle is-4 has-text-danger"
          v-if="!(menuGroups && menuGroups.length > 0)"
        >
          You do not have group items yet. Please create some
        </h4>
        <draggable
          v-else
          class="field is-grouped is-grouped-multiline"
          v-model="myList"
        >
          <div class="control" v-for="item in myList" :key="item.rank">
            <div class="tags are-large has-addons">
              <span class="tag is-rounded is-dark">
                {{ item.name }}
              </span>
              <a
                class="tag is-rounded is-delete"
                @click="deleteMenuGroup({ id: item.id })"
              ></a>
            </div>
          </div>
        </draggable>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import draggable from "vuedraggable";
import _ from "lodash";

import { getID } from "@/helpers/functions";

export default {
  name: "MenuItemsGroups",
  components: { draggable },

  data() {
    return {
      name: "",
    };
  },

  computed: {
    ...mapState(["menuGroups"]),

    myList: {
      get() {
        return _.orderBy(this.menuGroups, "rank", "asc");
      },
      set(value) {
        const temp = value.map((item, index) => ({
          ...item,
          rank: index + 1,
        }));
        this.saveMenuGroups(temp);
      },
    },
  },

  methods: {
    ...mapActions(["updateMenuGroup", "deleteMenuGroup", "saveMenuGroups"]),

    createMenuGroup(name) {
      const data = {
        id: getID(),
        name: name,
        // rank: this.menuGroups.length + 1,
        rank: _.maxBy(this.menuGroups, "rank")?.rank + 1 || 1,
      };
      this.updateMenuGroup(data);
      this.name = "";
    },
  },
};
</script>